<template>
    <div class="rootpage">
        <header ref="header">
            <el-menu ref="header"
                     :default-active="currentPageIndex"
                     class="el-menu-demo"
                     mode="horizontal"
                     @select="handleSelect"
                     background-color="#545c64"
                     text-color="#fff"
                     :router="true"
                     active-text-color="#ffd04b">
                <el-menu-item index="message"></el-menu-item>
                <el-menu-item index="debug"></el-menu-item>
                <el-menu-item index="setting"></el-menu-item>

            </el-menu>
        </header>

        <div class="left-nav" ref="leftNav">
            <el-menu
                    default-active="2"
                    class="el-menu-vertical-demo"
                    @open="handleOpen"
                    @close="handleClose"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b">
                <el-submenu index="1">
                    <template slot="title">
                        <i class="el-icon-location"></i>
                        <span></span>
                    </template>
                    <el-menu-item-group>
                        <template slot="title"></template>
                        <el-menu-item index="1-1">1</el-menu-item>
                        <el-menu-item index="1-2">2</el-menu-item>
                    </el-menu-item-group>
                    <el-menu-item-group title="2">
                        <el-menu-item index="1-3">3</el-menu-item>
                    </el-menu-item-group>
                    <el-submenu index="1-4">
                        <template slot="title">4</template>
                        <el-menu-item index="1-4-1">1</el-menu-item>
                    </el-submenu>
                </el-submenu>
                <el-menu-item index="2">
                    <i class="el-icon-menu"></i>
                    <span slot="title"></span>
                </el-menu-item>
                <el-menu-item index="3" disabled>
                    <i class="el-icon-document"></i>
                    <span slot="title"></span>
                </el-menu-item>
                <el-menu-item index="4">
                    <i class="el-icon-setting"></i>
                    <span slot="title"></span>
                </el-menu-item>
            </el-menu>
        </div>
        <div class="main" ref="main">
            <router-view/>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'HelloWorld',
        components: {},
        data() {
            return {
                currentPageIndex: "0"
            }
        },
        created() {
            this.resizeWindow()
        },
        methods: {
            resizeWindow() {
                console.log("this.$refs", this.$refs)
            },
            handleSelect(key, keypath) {
                console.log(key, keypath);
            },
            handleOpen() {
                console.log("handleOpen")
            },
            handleClose() {
                console.log("handleClose")
            }
        }
    }
</script>

<style scoped lang="scss">
    .rootpage {
        header {
            background-color: antiquewhite;
        }

        .left-nav {
            width: 20%;
            float: left;
        }
        .main {
            float: left;
            width: 80%;
        }

    }
</style>



